* {
  margin: 0;
  padding: 0;
  font-size: 10px;
  color: #ffffff;
  list-style: none;
  text-decoration: none; }

html,
body {
  background: #282c34; }

h4.title {
  margin: 5rem 0 3rem 0;
  font-size: 3rem;
  text-align: center; }

.explain {
  display: block;
  font-size: 2rem;
  margin: 1rem 0;
  border-bottom: .1rem solid #868991; }

a {
  font-size: 2rem; }

header {
  width: 100%;
  border-bottom: .2rem solid #ffffff;
  height: 6rem;
  position: fixed;
  top: 0;
  background: #282c34; }
  header .header-box {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%; }

footer {
  display: block;
  width: 100%;
  border-top: .1rem solid #ffffff;
  text-align: center; }

.introduce-box {
  margin-top: 10rem;
  height: 50rem; }
  .introduce-box .introduce {
    display: flex;
    align-items: center;
    justify-content: center; }
    .introduce-box .introduce .introduce-text {
      border-top: .2rem solid #ffffff; }
      .introduce-box .introduce .introduce-text ul li {
        font-size: 2rem;
        margin: 1rem 0; }

.portfolio-box {
  display: flex;
  align-items: center;
  justify-content: center; }
  .portfolio-box .portfolio-list > li {
    padding: 2rem;
    margin: 2rem;
    border-top: .1rem solid #ffffff;
    border-bottom: .1rem solid #ffffff; }
  .portfolio-box .portfolio-list ul,
  .portfolio-box .portfolio-list span {
    margin: 1rem; }
  .portfolio-box .portfolio-list .portfolio-img {
    display: block;
    width: 50rem;
    height: 30rem;
    background: url("../images/pp-main.png");
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center center; }

@keyframes opacity {
  0% {
    opacity: 0.8; }
  50% {
    opacity: 0; }
  100% {
    opacity: 0.8; } }
.ani-opacity-07 {
  animation: opacity;
  animation-duration: 2s;
  animation-delay: .7s;
  animation-iteration-count: infinite; }

.ani-opacity-1 {
  animation: opacity;
  animation-duration: 2s;
  animation-delay: 1s;
  animation-iteration-count: infinite; }

.ani-opacity-13 {
  animation: opacity;
  animation-duration: 2s;
  animation-delay: 1.3s;
  animation-iteration-count: infinite; }

.ani-opacity-15 {
  animation: opacity;
  animation-duration: 2s;
  animation-delay: 1.5s;
  animation-iteration-count: infinite; }

.ani-opacity-18 {
  animation: opacity;
  animation-duration: 2s;
  animation-delay: 1.8s;
  animation-iteration-count: infinite; }

